<template lang="html">
  <div class="tool-line">
    <img :src="collectImg" alt="" @click.stop="collection()">
    <!-- <img @click="toIgnore" src="../../assets/img/lahei-@3x.png" alt=""> -->
    <!-- <span></span> -->
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import gray from '@/assets/img/Collection-2@2x.png'
import red from '@/assets/img/Collection@2x.png'

export default {
  props: {
    isCollect: '',
    collectId: ''
  },
  computed: {
    userId: function () {
      return utils.userId()
    },
    collectImg: function () {
      return this.isCollect ? red : gray
    }
  },
  methods: {
    toIgnore () {
      minutils.Toast('')
      // minutils.MessageBoxConfirm({
      //   message: ', , ',
      //   callback: function (action) {
      //     action && console.log(action)
      //   }
      // })
    },
    collection () {
      // 
      if (this.isCollect) {
        this.deleteCollection(this.userId, this.collectId)
        console.log('calcle')
      } else {
        this.saveCollection()
      }
    },
    saveCollection () { // 
      let self = this
      minutils.MessageBoxConfirm({
        message: '',
        title: '',
        options: {
          confirmButtonText: '',
          cancelButtonText: ''
        },
        success (action) {
          self.$router.push('/movefriend')
        },
        fail (action) {
          console.log(action)
        }
      })
    },
    deleteCollection (userId, collectId) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '',
        title: '',
        options: {
          confirmButtonText: '',
          cancelButtonText: ''
        },
        success (action) {
          // minutils.Toast('')
          api.delCollectionById({userId: userId, ids: collectId}).then((response) => {
            minutils.Toast('')
            console.log(self.isCollect)
            self.$emit('reload')
          })
        },
        fail (action) {
          console.log(action)
        }
      })
    }
  }
}
</script>

<style lang="less">
  .tool-line {
    img {
      width: .65rem;
      height: .85rem;
      padding: .2rem;
      padding-right: 0;
      // margin-right: .3rem;
    }
    span {
      position: relative;
      top: -0.14rem;
    }
  }
</style>



